.top{
  position: absolute;
}
.top__item{
  z-index: 1;
  display: flex;
  gap: 20px;
  font-family: "Open Sans";
  font-weight: 700;
  color: #F6F3E4;
}

.bg{
  z-index: -1;
}
.toprated{
  position: absolute;
  top: 10px;
  font-family: "Open Sans";
  font-weight: 700;
  color: #F6F3E4;
  font-size: 30px;
}
.top__item-value{
  font-weight: 400;
}
.container{
  height: 70vh;
  overflow: scroll;
  width: 50vw;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}