.back{
  position: absolute;
  bottom: 0px;
  
}

.centered{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.colored{
  background-color: #FFB2B1;
  height: 100vh;
  width: 450px;
}
.gift{
  position: absolute;
  top: 10px;
}
.text{
  position: absolute;
  top: 100px;
}
.button{
  position: absolute;
}
.input{
  border-radius: 300px;
  position: absolute;
  padding: 10px;
  outline: none;
  border: 3px solid #1F1347;
  background-color: #FFB55E;
  color: #1F1347;
  width: calc(150px - 26px);
  font-family: "Open Sans";
  font-weight: 400;
  position: absolute;
  transform: translateY(-60px);
}
::-webkit-input-placeholder {
  color: #1F1347;
  font-family: "Open Sans";
  font-weight: 700;
  opacity: 0.8;
}
